  color: #545454;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.14;
}

.footer__link:hover .heart__svg, .footer__link:focus .heart__svg {
  transform-origin: center;
  animation: beat .35s infinite alternate;
}

.heart__svg {
  width: 14px;
  height: 12.75px;
  fill: #ff6b08;
  margin-left: 8px;
  margin-right: 8px;
}

.footer__text-first {
  margin-bottom: 10px;
  display: block;
}

footer button {
  color: #545454;
  background-color: rgba(0, 0, 0, 0);
  text-decoration: underline;
}

@media screen and (min-width: 768px) {
  footer {
    margin-top: 60px;
  }

  .footer__link {
    justify-content: center;
    align-items: center;
    font-size: 16px;
    line-height: 1.17;
    display: flex;
  }

  .footer__text-first {
    margin-bottom: 0;
  }
}

@keyframes beat {
  to {
    transform: scale(1.4);
  }
}

.mask {
  width: 100%;
  height: 100%;
  z-index: 1000;
  background-color: #fff;
  justify-content: center;
  align-items: center;
  transition: all .4s;
  display: flex;
  position: fixed;
  top: 0;
}

.loader {
  width: 100px;
  height: 100px;
  border: 16px solid #a70303;
  border-top-color: #a76161;
  border-radius: 50%;
  margin: 60px auto;
  animation: spin 1.5s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.hide {
  opacity: 0;
}

.backdrop-students {
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, .25);
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop-students.is-hidden-students {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.modal-students {
  opacity: 1;
  background-image: linear-gradient(to top left, #fdc830, #f37335);
  border-radius: 8px;
  padding: 20px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1), opacity .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)scale(1);
  box-shadow: 0 1px 3px rgba(0, 0, 0, .12), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px rgba(0, 0, 0, .2);
}

.backdrop-students.is-hidden-students .modal-students {
  opacity: 0;
  transform: translate(-50%, -50%)scale(.8);
}

.modal-students__close-btn {
  width: 30px;
  height: 30px;
  fill: #000;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  justify-content: center;
  align-items: center;
  padding: 0;
  transition: fill .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: absolute;
  top: 8px;
  right: 8px;
}

.modal-students__close-btn:hover, .modal-students__close-btn:focus {
  fill: var(--accent-color);
}

.students-list__title {
  text-align: center;
  padding-bottom: 20px;
  font-family: Roboto, sans-serif;
}

.students-list__title--text {
  color: #fff;
  background-color: #000;
  margin-right: 5px;
  padding-left: 5px;
  padding-right: 5px;
}

.students-list__title--text2 {
  background-color: var(--accent-color);
  color: #fff;
  margin-right: 10px;
  padding-left: 5px;
  padding-right: 5px;
}

.students-list__card {
  border: 3px solid #2c3e50;
  border-radius: 20px;
  overflow: hidden;
}

.students-list__content-wrapper {
  text-align: center;
  background-color: #d7dfe4;
  border-top: 3px solid #2c3e50;
  padding: 8px;
}

.students-list__name {
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-weight: 700;
}

.students-list__position {
  margin-right: 5px;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-weight: 400;
}

.students-list__link-wrapper {
  justify-content: center;
  display: flex;
}

.students-list__link {
  cursor: pointer;
  transition: fill .25s cubic-bezier(.4, 0, .2, 1);
}

.students-list__link:hover, .students-list__link:focus {
  fill: var(--accent-color);
}

@media screen and (max-width: 767px) {
  .backdrop-students {
    overflow-y: scroll;
  }

  .modal-students {
    width: 280px;
    height: 100%;
    overflow-y: scroll;
  }

  .students-list__card {
    margin-bottom: 20px;
  }
}

@media screen and (min-width: 768px) {
  .modal-students {
    width: 704px;
  }

  .students-list {
    flex-wrap: wrap;
    margin-top: -20px;
    margin-left: -20px;
    display: flex;
  }

  .students-list__card {
    flex-basis: calc(25% - 20px);
    margin-top: 20px;
    margin-left: 20px;
  }
}

.library-wrapper {
  text-align: center;
  margin: 0 auto;
  padding: 60px 0;
}

.library-text {
  margin-bottom: 30px;
  font-size: 30px;
}

.library-link {
  text-align: center;
  color: var(--primary-text-color);
  text-transform: uppercase;
  border: 1px solid var(--primary-text-color);
  background-color: rgba(0, 0, 0, 0);
  border-radius: 5px;
  padding: 16px 32px;
  transition: box-shadow .25s cubic-bezier(.4, 0, .2, 1), color .25s cubic-bezier(.4, 0, .2, 1), background-color .25s cubic-bezier(.4, 0, .2, 1), border .25s cubic-bezier(.4, 0, .2, 1);
}

.library-link:hover, .library-link:focus {
  color: var(--primary-bg-color);
  background-color: var(--accent-color);
  border: none;
  box-shadow: 0 3px 1px rgba(0, 0, 0, .1), 0 1px 2px rgba(0, 0, 0, .08), 0 2px 2px rgba(0, 0, 0, .12);
}

